import { DOMAIN, IS_DEV } from './constants';
import { HTTP } from './requests';

export function addOrUpdateArrayObject(arr: Array<Object>, obj: Object, key: string = 'slug', append: Boolean = false) {
  const arrKey = arr.map((i) => i[key]);

  if (!arrKey.includes(obj[key])) {
    if (append) return [...arr, obj];
    return [obj, ...arr];
  }

  return arr.map((i) => {
    if (i[key] === obj[key]) return obj;
    return i;
  });
}

export const getWindowQuery = () => new URL(window.location.href).searchParams;

export const getSharedData = (): Promise<Object> =>
  new Promise(async (resolve, reject) => {
    let sharedData = document.getElementById('sharedData');

    if (!sharedData && IS_DEV) {
      // This will not run in production
      console.log(`Getting page shared data in ${process.env.NODE_ENV} mode`);

      const base = new URL(window.location.href);
      const path = `${base.pathname}${base.search}`;

      let res;
      try {
        res = await HTTP.get(path);
      } catch (err) {
        return reject(err);
      }

      if (!res || res.status !== 200) return reject('No data');

      if (res.headers['content-type'].includes('text')) {
        const parser = new DOMParser();
        const doc = parser.parseFromString(res.data, 'text/html');
        sharedData = doc.getElementById('sharedData');
      }
    }
    if (sharedData) {
      return resolve(JSON.parse(sharedData.textContent));
    }

    return resolve({});
  });

export const getImgUrl = (src: string | null | undefined): string => {
  if (!src || !isString(src)) return '';

  if (IS_DEV && src && !src.includes('http')) {
    const domain = new URL(DOMAIN);
    return `${domain.origin}${src}`;
  }
  return src;
};

export const getClassName = (df: Array<string | boolean | null | undefined> = []) => {
  if (!Array.isArray(df)) return '';

  return df
    .map((i) => i && `${i}`)
    .filter(Boolean)
    .join(' ');
};

export const addForwardSlash = (str: string) => (str.endsWith('/') ? str : `${str}/`);

export const isRequired = (name = 'Param') => {
  throw new Error(`${name} is required`);
};

export const isString = (txt: any) => typeof txt === 'string' || txt instanceof String;

export const log = (...args: any[]) => IS_DEV && console.log(...args);
export const logInfo = (...args: any[]) => IS_DEV && console.info(...args);
export const logError = (...args: any[]) => IS_DEV && console.error(...args);
