import * as React from 'react';

import './table.scss';

import { getClassName as getCN, isFunction as isF } from '@miq/utiljs';

export interface TableProps extends React.ComponentPropsWithoutRef<'table'> {
  items: Object[];
  renderItem: (i: any) => JSX.Element | null;
  header?: React.ReactNode;
  children?: React.ReactNode;
  className?: string;
}

export default function Table({ children, ...props }: TableProps) {
  const { header, items, renderItem } = props;
  return (
    <table className={getCN(['miq-table', props.className])}>
      {header && (
        <thead className="miq-table-thead">
          <tr>{header}</tr>
        </thead>
      )}

      <tbody>{children || (Array.isArray(items) && isF(renderItem) && items.map((i) => renderItem(i)))}</tbody>
    </table>
  );
}

const Tr: React.FC<React.ComponentPropsWithoutRef<'tr'>> = ({ children, ...props }) => (
  <tr {...props} className={getCN(['miq-table-tr', props.className])}>
    {children}
  </tr>
);
Table.Tr = Tr;

const Td: React.FC<React.ComponentPropsWithoutRef<'td'>> = ({ children, ...props }) => (
  <td {...props} className={getCN(['miq-table-td', props.className])}>
    {children}
  </td>
);
Table.Td = Td;

const Th: React.FC<React.ComponentPropsWithoutRef<'th'>> = ({ children, ...props }) => (
  <th {...props} className={getCN(['miq-table-th', props.className])}>
    {children}
  </th>
);

Table.Th = Th;
