import axios from 'axios';
import { API_ENDPOINT, DOMAIN } from './constants';

axios.defaults.xsrfCookieName = 'csrftoken';
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN';
axios.defaults.headers['Content-Type'] = 'text/html; charset=UTF-8';
axios.defaults.headers.post['Content-Type'] = 'application/json';
// axios.defaults.headers.patch["Content-Type"] = "application/json";

export const HTTP = axios.create({
  baseURL: DOMAIN,
  timeout: 3000,
  withCredentials: true, // To enable cookies
  headers: { 'X-Requested-With': 'XMLHttpRequest', 'Content-type': 'application/json' },
  onUploadProgress: (progressEvt) => {
    // console.log(Math.round(progressEvt.loaded / progressEvt.total * 100) + '%')
  },
});

export const API = axios.create({
  baseURL: API_ENDPOINT,
  timeout: 3000,
  withCredentials: true,
  // headers: { "X-Requested-With": "XMLHttpRequest" },
  headers: {
    'Content-type': 'application/json',
  },
  // onUploadProgress: (progressEvt) => {},
});

export class Response {
  _res: any | null = null;

  constructor(res: any) {
    this._res = res;
  }

  first() {
    return this.items[0];
  }

  get items() {
    const data = this.data;
    if (!this.data) return null;
    return data.results;
  }

  get data() {
    if (!this.isSuccess) return null;
    return this._res.data;
  }

  get isSuccess(): boolean {
    const res = this._res;
    if (!res || !res.status || res.status >= 300) return false;
    return true;
  }

  get error() {
    if (this.isSuccess) return null;
    return this._res;
    // return RequestError(this._response, this._call.method, this._call.relativeUrl, this._call.body);
  }
}

export class AbstractRequest {
  constructor(endpoint) {
    this._path = null;
    this._params = Object.create(null);
    if (endpoint) this.endpoint = endpoint;
  }

  addParam(key, value) {
    this._params[key] = value;
    return this;
  }

  /**
   * @param {Object} params
   * @return this
   */
  setParams(params) {
    this._params = params;

    return this;
  }

  /**
   * @return {string}
   */
  get endpoint() {
    return this._endpoint;
  }

  /**
   * @return {string}
   */
  get path() {
    return this._path;
  }
}
