import * as Axios from 'axios';

import { log, logInfo, isString, addForwardSlash } from './helpers';

import { API, HTTP } from './requests';

export interface IModel {
  export: () => any;
}
export const Model = (data?: any) => {
  return Object.assign({}, data, {
    export() {
      return { ...data };
    },
  });
};

type SessionType = typeof HTTP;

export const CrudModel = (data: any = {}, _endpoint: string, _s: SessionType = HTTP) => {
  const { slug } = data;

  const isInstance: boolean = isString(slug);

  _endpoint = addForwardSlash(_endpoint);
  const _path: string | null = isInstance ? `${_endpoint}${slug}/` : null;

  /**
   * @constructor
   * @this CrudModel
   */

  function list(config?: Axios.AxiosRequestConfig): any {
    logInfo('Getting list', config);
    return this.get(_endpoint, config);
  }
  function detail(config?: Axios.AxiosRequestConfig) {
    if (!isInstance) throw new Error('slug required');
    return this.get(_path, config);
  }
  function get(path: string, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    logInfo('Getting', path, config);
    return _s.get(path, config);
  }

  function post(values: any, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    return this.postPath(_endpoint, values, config);
  }
  function postPath(path: string, values: any, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    logInfo('Posting', path, values);
    return _s.post(path, values, config);
  }

  function patch(values: any, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    if (!isInstance) throw new Error('Cannot perform patch: slug required');
    return this.patchPath(_path, values, config);
  }
  function patchPath(path: string, values: any, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    logInfo('Patching', path, values);
    return _s.patch(path, values, config);
  }

  function put(values: any, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    if (!isInstance) throw new Error('Cannot perform put: slug required');
    return this.putPath(_path, values, config);
  }
  function putPath(path: string, values: any, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    logInfo('Putting', path, values);
    return _s.put(path, values, config);
  }

  function _delete(config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    if (!isInstance) throw new Error('Cannot perform delete: slug required');
    return this.delete(_path, config);
  }
  function deletePath(path: string, config?: Axios.AxiosRequestConfig): Promise<Axios.AxiosResponse<any>> {
    logInfo('Deleting', path);
    return _s.delete(path, config);
  }

  const methods = { list, detail, get, post, postPath, put, putPath, patch, patchPath, delete: _delete, deletePath };
  return Object.assign({ _s, _endpoint, _path }, Model(data), methods);
};

export const StaffCrudModel = (data: any = {}, _endpoint: string, _s: SessionType = API) => {
  let { created, updated } = data;

  if (created) created = new Date(created);
  if (updated) updated = new Date(updated);

  return Object.assign({}, CrudModel(data, _endpoint, _s), { created, updated });
};

export class AbstractModel {
  // static get Fields() {
  //   return Object.freeze({})
  // }

  _data = {};
  _fields = [];

  constructor(data?: any) {
    if (data) Object.assign(this, data);
  }
}

//#region AbstractCrudModel

export class AbstractCrudModel extends AbstractModel {
  slug: string | undefined = undefined;
  _s: SessionType | undefined = undefined;
  _endpoint: string | undefined = undefined;

  constructor(data: any) {
    super(data);
    this.slug = data.slug;
  }

  static list(params: any) {
    return this.get(`${this.prototype._endpoint}`, params);
  }

  static detail(slug: string, params: any) {
    return this.get(`${this.prototype._endpoint}${slug}/`, params);
  }

  static get(path: string, params?: any) {
    const _s = this.prototype._s;
    if (!_s) throw new Error('Session missing');

    return new Promise((resolve, reject) => {
      _s.get(path, { params })
        .then((data) => resolve(data))
        .catch((err) => reject(err));
    });
  }

  static post(values: any, config?: Axios.AxiosRequestConfig) {
    const _endpoint = this.prototype._endpoint;
    if (!_endpoint) throw new Error('Endpoint missing');
    return this.postPath(_endpoint, values, config);
  }

  static postPath(path: string, values = {}, config?: Axios.AxiosRequestConfig) {
    const _s = this.prototype._s;
    if (!_s) throw new Error('Session missing');

    return new Promise((resolve, reject) => {
      _s.post(path, values, config)
        .then((data) => resolve(data))
        .catch((err) => reject(err));
    });
  }

  patch(values: Object, oldValues: Object | undefined | null, config?: Axios.AxiosRequestConfig) {
    log('Patchin', values);
    return this.patchPath(`${this._endpoint}${this.slug}/`, values, oldValues, config);
  }

  /**
   * Partially update object
   * @param   {Object}  values
   * @return  {Promise}
   */
  patchPath(path: string, values = {}, oldValues?: any, config?: Axios.AxiosRequestConfig) {
    console.log('Patchin path', path);
    return new Promise((resolve, reject) => {
      this._s
        .patch(path, values, config)
        .then((res) => resolve(res))
        .catch((err) => reject(err));
    });
  }

  /**
   * Update object
   * @param   {Object}  [params]
   * @return  {Promise}
   */
  put(values = {}): Promise<Object> {
    if (!this._s) throw new Error('Session required');
    return new Promise((resolve, reject) => {
      this._s
        .put(`${this._endpoint}${this.slug}/`, { ...this.exportData(), ...values })
        .then((res) => resolve(res))
        .catch((err) => reject(err));
    });
  }

  /**
   * Delete object
   * @param   {Object}  [params]
   * @return  {Promise}
   */
  delete(): Promise<any> {
    return this.deletePath(`${this._endpoint}${this.slug}/`);
  }

  deletePath(path: string) {
    log('Deleting path', path);
    return new Promise((resolve, reject) => {
      this._s
        .delete(path)
        .then((res) => resolve(res))
        .catch((err) => reject(err));
    });
  }
}

Object.assign(AbstractCrudModel.prototype, { _endpoint: null, _s: HTTP });

//#endregion AbstractCrudModel

//#region AbstractCrudStaffModel

export class AbstractCrudStaffModel extends AbstractCrudModel {}
AbstractCrudStaffModel.prototype._s = API;

//#endregion AbstractCrudStaffModel
