export const IS_DEV: boolean = process.env.NODE_ENV !== 'production';

export const DOMAIN: string = IS_DEV
  ? process.env.REACT_APP_DOMAIN || `http://127.0.0.1:8000`
  : `${window.location.origin}`;
// export const DOMAIN = IS_DEV ? `http://192.168.1.243:8000` : `${window.location.origin}`;

export const API_ENDPOINT: string = `${DOMAIN}${process.env.REACT_APP_API_ENDPOINT}`;
export const STAFF_PATH: string = process.env.REACT_STAFF_PATH || '/staff/';
