import { getClassName as getCN } from '@miq/utiljs';
import React from 'react';
import { Link } from 'react-router-dom';

import './views.scss';

interface IViewHeaderProps extends Omit<React.ComponentPropsWithoutRef<'div'>, 'title'> {
  text?: string;
  back?: string;
  title?: string | React.ReactNode;
  header?: React.ReactNode;
  actions?: React.ReactNode;
  headerCN?: string;
}

export interface IViewProps extends IViewHeaderProps {
  loading?: boolean | string;
  footer?: React.ReactNode;
  mainCN?: string;
  footerCN?: string;
}

export type IViewType = typeof View & { TabSection: React.FC };

export const View = ({ children, ...props }: IViewProps) => {
  const { header, title, text, actions, back, footer, loading, mainCN, footerCN, headerCN, ...rest } = props;
  return (
    <div {...rest} className={getCN(['miq-view', props.className])}>
      <Header {...{ header, title, text, actions, back, headerCN }} />

      <div className={getCN(['miq-view-main', mainCN])}>{children}</div>

      {footer && <footer className={getCN(['miq-view-footer', footerCN])}>{footer}</footer>}
    </div>
  );
};

const Header = ({ header, title, text, actions, back, headerCN }: IViewHeaderProps) => {
  const hasHeader = header || title || text || actions || back;

  if (!hasHeader) return null;

  if (header) return <div className={getCN(['miq-view-header-wrapper', headerCN])}>{header}</div>;

  return (
    <header className={getCN(['miq-view-header-wrapper', headerCN])}>
      <div className="miq-view-header d-flex">
        <div className="d-flex align-items-center">
          {back && (
            <Link to={back} className="btn miq-view-header-back me-1" title="Go back">
              {`<`}
            </Link>
          )}

          <div className="miq-view-header-title">
            {typeof title === 'string' ? <div className="miq-view-header-title-text">{title}</div> : title}
          </div>
        </div>

        {actions && <section className="miq-view-header-actions">{actions}</section>}
      </div>

      {text && <div className="miq-view-header-text">{text}</div>}
    </header>
  );
};

export interface TabSectionProps {
  children: React.ReactNode;
}

const TabSection = ({ children, ...props }: TabSectionProps) => {
  return <div className="">{children}</div>;
};

View.TabSection = TabSection;
