import * as React from 'react';
import { useNavigate, NavLink, NavLinkProps } from 'react-router-dom';

import './buttons.scss';

import { getClassName as getCN, isFunction as isF, getWindowQuery, log } from '@miq/utiljs';
import { ModalWidthTypes, Portal } from '../Modal';

//#region Button

export interface ButtonProps extends React.ComponentPropsWithRef<'button'> {}

export const Button: React.FC<ButtonProps> = React.forwardRef(({ children, ...props }, ref) => {
  return (
    <button type="button" {...props} className={getCN(['btn', props.className])} ref={ref}>
      {children}
    </button>
  );
});

Button.displayName = 'Button';

//#endregion Button

//#region QueryButton

interface QueryButtonProps extends ButtonProps {
  query?: any;
  navOnClick?: boolean;
  onClick?: (q: any) => void;
}

export const QueryButton: React.FC<QueryButtonProps> = React.forwardRef((props, ref) => {
  const navigate = useNavigate();
  let { query, onClick, navOnClick = true, ...rest } = props;
  if (!query) query = getWindowQuery();

  const handleClick = (query: any) => {
    onClick?.({ query });
    if (!navOnClick) return;
    const path = new URL(window.location.href);

    navigate(`${path.pathname}?${query}`);
  };

  return <Button {...rest} onClick={handleClick.bind(null, query)} ref={ref} />;
});

//#endregion QueryButton

//#region ConfirmButton

export interface ConfirmButtonProps extends ButtonProps {
  readonly modal?: { width: ModalWidthTypes };
  render: (T: { isOpen: boolean; setOpen: (v: boolean) => void }) => void;
  renderHeader: (T: { isOpen: boolean; setOpen: (v: boolean) => void }) => void;
  renderFooter: (T: { isOpen: boolean; setOpen: (v: boolean) => void }) => void;
}

export const ConfirmButton: React.FC<ConfirmButtonProps> = ({
  children,
  render,
  renderHeader,
  renderFooter,
  ...props
}) => {
  const [isOpen, setOpen] = React.useState<boolean>(false);
  const { modal, ...rest } = props;
  return (
    <>
      <Button {...rest} onClick={() => setOpen(!isOpen)}>
        {children}
      </Button>
      {isOpen && (
        <Portal>
          <Portal.Modal
            {...modal}
            width={modal?.width}
            header={isF(renderHeader) && renderHeader({ isOpen, setOpen })}
            footer={isF(renderFooter) && renderFooter({ isOpen, setOpen })}
          >
            {isF(render) && render({ isOpen, setOpen })}
          </Portal.Modal>
        </Portal>
      )}
    </>
  );
};

//#endregion ConfirmButton

